
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.container {
  max-width: 1170px;
  padding: 0 "20px";
  margin: 0 auto;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.grid [class*="col-"] {
    padding: 0 "20px";
}
.grid .col-3 {
    width: 100%;
}
@media screen and (min-width: 768px) {
.grid .col-3 {
        width: 50%;
}
}
@media screen and (min-width: 992px) {
.grid .col-3 {
        width: 25%;
}
}



